import React,{Component} from "react";
import PropTypes from "prop-types";
import "./operate.scss";

//
class Operate extends Component{
    static contextTypes = {
        router:PropTypes.object.isRequired
    }
    constructor(props){
        super(props)
        this.state = {
            histroy:"",
            readerText:""
        }
    }
    componentWillMount(){
        const localHistory = localStorage.getItem("history");
        const id =  this.context.router.route.match.params.id;
        let bookId = "";
        if(JSON.parse(localHistory)){
            bookId = JSON.parse(localHistory)[id]
        }
        if(bookId &&  this.props.payFlag === "payed"){
            this.setState({
                readerText:""
            })
        }
    }
    render(){
        let panel1= null,panel2 = null;
        if(this.props.payFlag === "payed"){
            panel1 = <span className="go-on">{this.state.readerText}</span>
        }else{
            panel1 = <span className="try-read"></span>
            panel2 = <span className="pay-for-price"> 9.9</span>
        }
        return(
            <div className="operate-container">
                {panel1}{panel2}
            </div>
        )
    }
}

export default Operate;